<template>
  <!-- 公共打印 -->
  <div v-show="false">
    <div v-if="params!=null">
      <div id="print-body">
        <TABLE border=1 cellSpacing=0 cellPadding=1 width="100%" style="border-collapse:collapse;word-break:break-all;word-wrap:break-word" bordercolor="#333333">
          <thead>
            <TR>
              <TD :width="val.width" v-for="(val,index) in printData.printBody" :key="index">

                <DIV align=center><b>{{val.name}}</b></DIV>
              </TD>
            </TR>
          </thead>
          <TBODY>
            <TR v-for="(item,i) in tableData" :key="item.id">
              <td align="center" v-for="val in printData.printBody" :key="val">
                <font>{{val.value?item[val.value]:i+1}}</font>
              </td>
            </TR>
          </TBODY>

          <tfoot>
            <tr>
              <td align="center" v-for="(val,index) in printData.printBody" :key="index">
                <font>{{val.formatter ? updateThis(val.formatter,[params[val.sum], params, val, allData]) : val.sum ? (val.sum!='合计' ? params[val.sum] : '合计' ):''}}</font>
              </td>
            </tr>
          </tfoot>
        </TABLE>
      </div>
      <div id="print-footer">
        <TABLE border=0 cellSpacing=0 cellPadding=0 width="100%">
          <TBODY>
            <TR v-for="(tr,index) in printData.printFooter" :key="index">
              <TD v-for="(td,i) in tr" :key="i" :colspan="td.colspan?td.colspan:0">
                <font>{{td.label}}:
                  <SPAN>{{td.formatter ? td.formatter(params[td.name],params, tr, allData): td.name!=""?params[td.name]:'_______'}}</SPAN>
                </font>
              </TD>
            </TR>
          </TBODY>
        </TABLE>
      </div>
      <div id="print-header">
        <DIV style="LINE-HEIGHT: 30px" class=size16 align=center><STRONG>
            <font style="font-size:18px">{{params.company_name}}{{printData.printHeader.title}}</font>
          </STRONG></DIV>
        <TABLE border=0 cellSpacing=0 cellPadding=0 width="100%">
          <TBODY>
            <TR v-for="(tr,index) in printData.printHeader.Header" :key="index">
              <TD v-for="(td,i) in tr" :key="i" :colspan="td.colspan?td.colspan:0">
                <font>{{td.label}}:
                  <SPAN>{{ td.formatter ? td.formatter(td, params, allData) : params[td.name]}}</SPAN>
                </font>
              </TD>
            </TR>
          </TBODY>
        </TABLE>
      </div>
    </div>
  </div>
</template>
<script>
import { getLodop } from "common/utils/LodopFuncs";
import { mapActions } from "vuex";
import printDatas from "./printData.js";
export default {
  data() {
    return {
      P_ID: null,
      params: null,
      refundArry: [],
      currentIndex: 0,
      printsNumber: 0,// 记录打印接口次数
      dataI: 0,
      outname: "",
      printData: {},
      indexStatus: 0,
      status: false,
      contains: [
        {
          typeName: "outboundOrder", // 销售出库单
          id: "outstock_id",
          url: "printOutstock",
          type: 1,
          params: {
            primary_id: 1
          }
        },
        {
          typeName: "autoPartsList", // 销售汽配单
          id: "outstock_id",
          url: "printOutstock",
          type: 1,
          params: {
            primary_id: 2
          }
        },
        {
          typeName: "salesPickingOrders", // 销售拣货单
          id: "outstock_id",
          url: "printOutstock",
          type: 1,
          params: {
            primary_id: 3
          }
        }
      ],
      tableData: null
    };
  },
  props: {
    title: String,
    ExcludeFooter: {
      type: Array,
      default: () => {
        return [];
      }
    },
    fieldUrl: {
      // 打印权限
      type: String,
      default: ""
    },
    returnParams: {
      // 解决后台字段不同
      type: String,
      default: "info"
    },
    returnTableData: {
      // 解决后台字段不同
      type: String,
      default: "goods_list"
    }
  },
  watch: {
    title(newVal) {
      this.init(newVal);
    }
  },
  methods: {
    ...mapActions(["printingStatus"]),
    printPdf(val, index = 0, callBack) {
      let url = "";
      var params = this.$publicFution.deepCopy(val);
      if (this.status) {
        this.refundArry.push(val[this.contains[this.indexStatus].id]);
        url = this.contains[this.indexStatus].url;
        params = Object.assign(params, this.contains[this.indexStatus].params);
      } else {
        this.refundArry.push(val[this.printData.printHeader.data.id]);
        url = this.printData.printHeader.data.url;
      }
      // console.log(val.name);
      // this.outname = val.name;
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api[url],
        params: {
          ...params,
          repeat: true
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          if (this.status) {
            this.printData = this.$publicFution.deepCopy(data.data.tmp);
            this.initPrint();
          }
          this.params = data.data[this.returnParams] ? data.data[this.returnParams] : data.data; // 解决后台字段不同
          this.tableData = data.data[[this.returnTableData]]; // 解决后台字段不同
          this.allData = data.data;
          this.$nextTick(() => {
            vm.CreatePrintPage(index);
          });
          callBack && callBack();
          if (this.fieldUrl) {
            this.dealPower(data.data.notFieldAuthHeader);
          }
        }
      }).catch(() => {
        callBack && callBack();
      });
    },
    // 改变this指向
    updateThis (func,args) {
      return func.apply(this, args)
    },
    // 处理打印权限
    dealPower(arr) {
      // 处理表头
      if (this.printData.printHeader && this.printData.printHeader.Header) {
        this.printData.printHeader.Header = printDatas[
          this.title
        ].printData.printHeader.Header.map(val => {
          return val.filter(twoVal => {
            for (var i = 0; i < arr.length; i++) {
              if (arr[i] === twoVal.name) {
                return false;
              }
            }
            return true;
          });
        });
      }
      // 处理表尾
      if (this.printData.printFooter && this.printData.printFooter) {
        this.printData.printFooter = printDatas[
          this.title
        ].printData.printFooter.map(val => {
          return val.filter(twoVal => {
            for (var i = 0; i < arr.length; i++) {
              if (arr[i] === twoVal.name) {
                return false;
              }
            }
            return true;
          });
        });
      }
    },
    printList(list, val, index = 0) {
      var length = list.length;
      var params = {};
      if (Array.isArray(val)) {
        val.forEach(key => {
          params[key] = list[index][key];
        });
      }
      this.printPdf(params, index === 0 ? true : index, () => {
        if (index < length - 1) {
          this.printList(list, val, index + 1);
        }
      });
    },
    CreatePrintPage(index) {
      var LODOP = getLodop();
      LODOP.PRINT_INIT(this.printData.printHeader.title);
      LODOP.SET_PRINT_MODE("AUTO_CLOSE_PREWINDOW", 1); // 打印后自动关闭预览
      var strStyle =
        "<style> table,td,th {border-width: 1px;border-style: solid;border-collapse: collapse;}</style>";
      LODOP.ADD_PRINT_TABLE(
        //intTop,intLeft,intWidth,intHeight,strHtml
        110,
        "1.875%",
        "95%",
        374,
        strStyle + document.getElementById("print-body").innerHTML
      ); //文本主体
      LODOP.SET_PRINT_STYLEA(0, "Vorient", 3); //数字型，0--上边距锁定 1--下边距锁定 2--垂直方向居中 3--上边距和下边距同时锁定（中间拉伸），缺省值是0
      LODOP.ADD_PRINT_HTM(
        6,
        "2.25%",
        "95%",
        77,
        document.getElementById("print-header").innerHTML
      ); //文本头部
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.ADD_PRINT_HTM(
        489,
        19,
        "92.625%",
        80,
        document.getElementById("print-footer").innerHTML
      ); //文本尾部
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.SET_PRINT_STYLEA(0, "Horient", 2);
      LODOP.SET_PRINT_STYLEA(0, "Vorient", 1);
      LODOP.SET_PRINT_STYLEA(0, "LinkedItem", 1);

      LODOP.ADD_PRINT_HTM(
        1,
        680,
        200,
        100,
        `<span style="font-size:15px">第${this.params.print_times}次打印</span>`
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.SET_PRINT_STYLEA(0, "Horient", 0);

      LODOP.ADD_PRINT_HTM(
        3,
        34,
        196,
        20,
        "<font><span tdata='pageNO'>第##页</span>/<span tdata='pageCount'>共##页</span></font>"
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      let This = this;
      // LODOP.PRINT_DESIGN();
      if (index) {
        LODOP.PRINT();
      } else {
        LODOP.PREVIEW();
      }
      LODOP.On_Return_Remain = true;
      LODOP.On_Return = function(TaskID, Value) {
        This.P_ID = Value;
        This.dataI++;
        if (Value == 1) {
          This.$axMethod
            .printTimesAdd({
              bill_id: This.refundArry.length
                ? This.refundArry[This.currentIndex]
                : This.dataI,
              name: This.outname,
              type: This.printData.printHeader.data.type,
              hideLoding: true
            })
            .then(data => {
              This.printsNumber++
              if (This.printsNumber >= This.refundArry.length) {
                This.$emit("Search", "");
                This.currentIndex = 0
                This.printsNumber = 0
                This.refundArry = []
              }
            });
          // alert("打印成功！");
          // This.printingStatus(false);
        } else if (Value == 0) {
          // This.printingStatus(false);
          // alert("打印任务被删除！");
        }
        This.currentIndex++;
      };
    },
    initPrint() {
      if (this.printData.printFooter) {
        this.printData.printFooter = this.printData.printFooter.map(item => {
          if (Array.isArray(item)) {
            item = item.filter(twoItam => {
              if (!this.ExcludeFooter.includes(twoItam.name)) {
                return true;
              }
            });
          }
          return item;
        });
      }
    },
    getFieldpower() {
      this.$axios({
        url: "v2/open/FieldAuth/getFieldList",
        method: "get",
        params: {
          menu_action: this.fieldUrl
        }
      }).then(({ data }) => {
        if (data.code) {
          if (this.printData.printBody) {
            this.printData.printBody = this.printData.printBody.filter(val => {
              for (var j = 0; j < data.data.length; j++) {
                if (data.data[j].field === val.value) {
                  return true;
                }
              }
            });
          }
        }
      });
    },
    init() {
      if (this.title) {
        this.status = this.contains.some((item, index) => {
          this.indexStatus = index;
          return item.typeName == this.title;
        });
        if (!this.status) {
          this.printData = this.$publicFution.deepCopy(
            printDatas[this.title].printData
          );  
          this.initPrint();
        }
      }
      if (this.fieldUrl) {
        this.getFieldpower();
      }
    }
  },
  mounted() {
    this.init();
  }
};
</script>
